import React, { useRef, useState } from 'react';
import { Swiper, SwiperSlide } from 'swiper/react';
import 'swiper/css';
import 'swiper/css/pagination';
import 'swiper/css/navigation';
import { Autoplay, Pagination, Navigation } from 'swiper/modules';

function Slider() {
  return (
    <div className="container mx-auto p-10">
      <Swiper
        spaceBetween={30}
        loop={true}
        centeredSlides={true}
        autoplay={{
          delay: 2500,
          disableOnInteraction: false,
        }}
        pagination={{
          clickable: true,
        }}
        navigation={false}
        modules={[Autoplay, Pagination, Navigation]}
        className="mySwiper"
      >
       <SwiperSlide><img src="/Image/pic1-1.png" class="w-3/4 mx-auto h-96"/></SwiperSlide>        
       <SwiperSlide><img src="/Image/pic1-2.png" class="w-3/4 mx-auto h-96"/></SwiperSlide>        
       <SwiperSlide><img src="/Image/pic1-3.png" class="w-3/4 mx-auto h-96"/></SwiperSlide>        
       <SwiperSlide><img src="/Image/pic1-4.png" class="w-3/4 mx-auto h-96"/></SwiperSlide>        
      </Swiper>
    </div>
  );
}
export default Slider